<template>
  <div class="playlist">
    <div style="width: 100%; overflow: hidden">
      <div
        class="x-box"
        :style="{
          background: 'url(' + coverImg + ')',
        }"
      ></div>
      <div class="imgs">
        <div class="imgpi">
          <img :src="coverImg" />
          <span class="play-count">{{
            playCount | formatPlayCount
          }}</span>
        </div>
        <div class="songs">歌单</div>
        <div class="plhead_fr">
          <h1>{{ names }}</h1>
        </div>
      </div>
    </div>
    <div class="list">歌曲列表</div>
    <ul class="Piaul">
      <HotSong
        @update:music="$emit('update:music', $event)"
        @update:PicturePath="$emit('update:PicturePath', $event)"
        @update:playlist="$emit('update:playlist', SongListDetails)"
        v-for="(ind, index) in SongListDetails"
        :key="ind.id"
        :index="index"
        :item="ind"
        :currentMusic="$attrs.currentMusic"
        :paused="$attrs.paused"
      />
    </ul>
  </div>
</template>

<script>
import HotSong from "@/components/HotSong.vue";

export default {
  components: {
    HotSong,
  },
  data() {
    return {
      SingleGroup: [],
      SongListDetails: [],
      coverImg: "",
      playCount:"",
      names:"",
    };
  },
  filters: {
    formatPlayCount: function (value) {
      return (value / 10000).toFixed(1) + "万";
    },
  },
  activated() {
    this.axios
      .get("/playlist/detail?id=" + this.$route.query.id)
      .then((response) => {
        // console.log(response.data.playlist);
        this.SingleGroup = response.data;
        this.coverImg = response.data.playlist.coverImgUrl
        this.playCount = response.data.playlist.playCount
        this.names = response.data.playlist.name
        this.axios 
          .get(
            "/song/detail?ids=" +
              response.data.playlist.trackIds
                .slice(0, 20)
                .map((e) => e.id)
                .join()
          )
          .then((response) => {
            // console.log(response.data.songs);
            this.SongListDetails = response.data.songs;
          })
          .finally(() => {
            // console.log(321);
            this.$root.isShowLoading = false;
          });
      })
      .finally(() => {
        // console.log(222);
        this.$root.isShowLoading = true;
      });
  },
};
</script>

<style lang="less">
.playlist {
  position: relative;
  .x-box {
    width: 100%;
    height: 174px;
    filter: blur(20px);
    &:after {
      content: " ";
      background-color: rgba(0, 0, 0, 0.25);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }
  }
  .imgs {
    position: absolute;
    top: 10px;
    left: 5%;
    div {
      float: left;
      color: white;
      h1 {
        word-break: break-all;
      }
      img {
        width: 7.125rem;
        height: 7.125rem;
      }
    }
    .songs {
      width: 2.5rem;
      height: 1.0625rem;
      position: absolute;
      top: 5px;
      background: red;
      font-size: 12px;
      border-radius: 0 10px 10px 0;
      text-align: center;
    }
    .imgpi {
      position: relative;
      .play-count {
        position: absolute;
        top: 2px;
        right: 0;
        font-size: 12px;
        &::before {
          content: "";
          background: no-repeat
            url("data:image/svg+xml;base64,");
          background-position: left center;
          background-size: auto 100%;
          width: 1.2em;
          height: 0.9em;
          display: inline-block;
        }
      }
    }
    .plhead_fr {
      width: 10.5rem;
      height: 9rem;
      margin-left: 16px;
    }
  }
  .list {
    width: 100%;
    height: 23px;
    line-height: 23px;
    padding-left: 5px;
    background: #eeeff0;
    color: #666;
    font-size: 12px;
    z-index: 8;
  }
  .Piaul {
    width: 100%;
  }
}
</style>